<template>
  <div style="width: 100vw;padding-top: 20vh;background-size: cover;padding-bottom: 3vw;margin-top: -70px;">
    <div class="content2">
      <video ref="videoPlayer" src="@/assets/vids/rick.mp4" id="MainVideovid"
        style="display: none;margin: auto;width: 100%;border-radius: 10px;box-shadow: 0px 0px 20px 10px rgb(0 0 0 / 26%);"></video>
      <p id="GATRICK" style="display: none">Chill</p>
    </div>

    <div class="panel" :class="{ 'show': isPanVis }">
      <div class="panel-con fade" @click="accept">
        <div class="panel-hea">
          <h5 class="panel-tit">Chúng ta không sử dụng Bánh Quy!</h5>
          <button type="button" class="buClose" @click="accept">x</button>
        </div>
        <div class="panel-bod">
          <p>Như tiêu đề, chúng "ta" không sử dụng BÁNH QUY!</p>
          <p>Để tiếp tục, vui lòng chấp nhận bị <a href="#">Rich Roll</a></p>
        </div>
        <div class="panel-foo">
          <button type="button" @click="accept">Sao lại có nhỉ</button>
          <button type="button" @click="accept">Chấp nhận</button>
        </div>
      </div>
    </div>
    <div class="panel-bg fade" :class="{ 'show': isPanVis }"></div>
  </div>
</template>

<script>
export default {
  name: "RickRoll",
  data() {
    return {
      isPanVis: false,
      click: false,
    };
  },
  mounted() {
    this.$refs.videoPlayer.preload = "auto";
    setTimeout(() => { this.showPanel(); }, 500);
  },
  methods: {
    showPanel() {
      this.isPanVis = true;
    },
    closepanel() {
      this.isPanVis = false;
    },
    accept() {
      this.closepanel();
      this.click = true;
      this.playVideo();
    },
    playVideo() {
      if (this.click) {
        this.$refs.videoPlayer.style.display = "";
        this.$refs.videoPlayer.play();
        document.getElementById("GATRICK").style.display = "";
        this.$refs.videoPlayer.scrollIntoView();
      }
    }
  }
};
</script>

